.modal-info {
    &__close {
        position: absolute;
        top: 10px;
        right: 20px;
        font-size: 24px;
    }
    &__content {
        padding: 50px 20px 50px;
        text-align: center;
        border-radius: 5px;
        box-shadow: 0 7px 36px 0 rgba(48, 48, 48, 0.13);
        border: solid 1.1px $content-border-color;
    }
    &__icon {
        color: #2c4c5b;
        font-size: 40px;
    }
    &__title {
        color: #2c4c5b;
        font-size: 16px;
        margin-top: 20px;
        font-weight: bold;
        font-family: $font-family-secondary;
    }
    &__text {
        color: $gray-300;
        font-size: $font-size-sm;
        margin-top: 10px;
        font-family: $font-family-secondary;
    }
    &__button {
        margin-top: 72px;
    }
}
