.main-analysator-sidebar__head_clause {
  color: #8500ff;
}

.main-analysator-sidebar__head_anomaly {
  color: #ff0500;
}

.main-analysator-sidebar__head_date {
  color: #ff8500;
}

.main-analysator-sidebar__list {
  margin-left: 15px;
  font-family: "SF UI Text", sans-serif;
  font-weight: 500;
  padding: 25px 0;
  font-size: 0.6875rem;
}

.main-analysator-sidebar__list, .main-analysator-sidebar__list a {
  color: #9797a6;
}

.main-analysator-sidebar__item {
  margin: 0 0 13px;
}

.main-analysator .highlight:before {
  color: #fff;
  display: block;
  position: absolute;
  left: -30px;
  margin-top: -16px;
  line-height: 16px;
  padding: 0 8px;
  border-radius: 10px;
  font-family: "SF UI Text", sans-serif;
  font-weight: bold;
}

.main-analysator .highlight_law {
  background-color: #cdccff;
}

.main-analysator .highlight_law:before {
  content: 'Law';
  background-color: #0500ff;
}

.main-analysator .highlight_clause {
  background-color: #e7ccff;
}

.main-analysator .highlight_clause:before {
  content: 'Clause';
  background-color: #8500ff;
}

.main-analysator .highlight_anomaly {
  background-color: #ffcdcc;
}

.main-analysator .highlight_anomaly:before {
  content: 'Anomaly';
  background-color: #ff0500;
}

.main-analysator .highlight_date {
  background-color: #ffe7cc;
}

.main-analysator .highlight_date:before {
  content: 'Date';
  background-color: #ff8500;
}

.partial-section {
  position: relative;
  font-family: "SF UI Text", sans-serif;
  box-shadow: 0 7px 36px 0 rgba(48, 48, 48, 0.1);
  border: solid 1px #efefef;
  background-color: #fff;
  border-radius: 3px;
  padding: 16px;
}

.partial-section h2 {
  color: #304f5f;
  letter-spacing: 0.22px;
  margin-bottom: 20px;
}

.partial-section__expand {
  position: absolute;
  z-index: 10;
  cursor: pointer;
  border: none;
  outline: none;
  top: 8px;
  right: 8px;
  background: url("data:image/png;base64,");
  width: 16px;
  height: 16px;
  overflow: hidden;
}

.main-auth {
  font-family: "SF UI Text", sans-serif;
  margin-top: 65px;
  height: 736px;
  border-radius: 3px;
  box-shadow: 0 7px 36px 0 rgba(48, 48, 48, 0.13);
  border: solid 1px #efefef;
  background-color: #fff;
}

.main-auth__white {
  position: relative;
  border: solid 1px #efefef;
  height: 100%;
  z-index: 1;
}

.main-auth__grey {
  position: relative;
  background-color: #f2f6fa;
  height: 100%;
}

.main-auth__grey:before {
  content: '';
  position: absolute;
  background-image: url(../assets/img/auth-path.png);
  width: 411px;
  height: 361px;
  bottom: 0;
  right: -113px;
}

.main-auth__small {
  margin-bottom: 30px;
}

.main-auth__title {
  margin-bottom: 32px;
}

.main-auth__content {
  width: 350px;
  max-width: 100%;
  padding: 0 10px 75px;
  margin: 0 auto;
}

.main-auth__submit {
  margin-top: 40px;
  text-align: center;
}

.main-auth__submit .btn {
  width: 175px;
  height: 40px;
  border-radius: 20px;
  text-transform: uppercase;
  font-size: 0.6875rem;
  font-weight: bold;
}

.main-auth__info {
  margin-top: 22px;
  text-align: center;
}

.main-auth__info a {
  color: #007aff;
  font-weight: bold;
}

.main-auth_subscribe .main-auth__content {
  padding-bottom: 50px;
}

.main-auth_subscribe .main-auth__grey:before {
  transform: scaleX(-1);
  right: auto;
  left: -113px;
}

.main-auth_subscribe .main-auth__title {
  margin-bottom: 15px;
  line-height: 24px;
  color: #315262;
}

@media (max-width: 991.98px) {
  h1, .h1 {
    font-size: 1.0625rem;
  }
  h2, .h2 {
    font-size: 0.9375rem;
  }
  .main-actions .button-action {
    margin: 0;
  }
  .main-analysator {
    margin-top: 32px;
  }
  .main-analysator-container {
    margin-right: 0;
    padding: 20px 30px;
  }
  .main-analysator-content {
    padding: 10px;
  }
  .main-analysator-sidebar {
    float: none;
    width: auto;
    padding-left: 0;
    margin-bottom: 15px;
  }
  .main-analysator-sidebar__head {
    margin-bottom: 5px;
  }
  .main-analysator-sidebar__list {
    padding: 5px 0 10px;
  }
  .main-analysator-sidebar__item {
    margin: 5px 0;
  }
  .main-analysator .highlight:before {
    left: -50px;
  }
  .main-auth {
    margin-top: 10px;
  }
}

@media (max-width: 767.98px) {
  h1, .h1 {
    font-size: 1rem;
  }
  h2, .h2 {
    font-size: 0.875rem;
  }
  .main-content {
    margin: 20px 0;
  }
  .main-content-head-folder {
    margin: 10px 0;
  }
  .main-section {
    padding: 20px;
  }
  .main-section__title-left {
    padding-left: 0;
    margin-bottom: 10px;
  }
  .main-actions {
    margin-left: 0;
  }
  .button-action {
    margin: 0;
  }
  .pagination {
    justify-content: center;
  }
}
