$component-selector: '.header';

#{$component-selector} {
    @include clearfix;
    color: $white;
    padding: 25px 0;
    a:not(.btn) {
        &, &:hover {
            text-decoration: none;
            color: $white;
        }
        &:hover {
            opacity: 0.8;
        }
    }
    &-logo {
        width: 197px;
        &_margin {
            margin: 25px 0 0 110px;
        }
    }
    &-search {
        position: relative;
        width: 259px;
        &__field {
            font-family: $font-family-secondary;
            border: 0;
            width: 259px;
            padding-right: 35px;
            height: 31px;
            line-height: 31px;
            border-radius: $border-radius;
            font-size: $font-size-xs;
            &, &:hover, &:focus {
                color: $white;
                background-color: rgba(255, 255, 255, 0.2);
            }
            &::placeholder {
                color: rgba(255, 255, 255, 0.75);
            }
        }
        &__submit {
            position: absolute;
            top: 0;
            right: 0;
            border: 0;
            outline: none;
            padding: 5px 15px;
            cursor: pointer;
            background-color: transparent;
            color: #d6e9fe;
            &:hover {
                opacity: .8;
            }
        }
    }
    &-user {
        font-size: $font-size-xs;
        margin-top: 5px;
        margin-right: 10px;
        line-height: 20px;
        &__name {
            font-family: $font-family-secondary;
            letter-spacing: 0.2px;
        }
        &__circle {
            display: inline-block;
            width: 29px;
            height: 29px;
            line-height: 29px;
            text-align: center;
            background-color: rgba(255, 255, 255, 0.2);
            border-radius: 50%;
            vertical-align: top;
            position: relative;
            top: -5px;
            margin-left: 10px;
            font-weight: bold;
        }
    }
    &-help {
        position: relative;
        font-size: $font-size-xs;
        line-height: 20px;
        margin-top: 5px;
        margin-right: 13px;
        padding-right: 13px;
        &::after {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            width: 1px;
            height: 24px;
            overflow: hidden;
            background-color: #5da9f9;
        }
        &__icon {
            font-size: 19px;
            vertical-align: top;
            margin-right: 3px;
        }
    }
    &-subscribe {
        margin-right: 20px;
    }
}

@include media-breakpoint-down(lg) {
    #{$component-selector} {
        &-logo_margin {
            margin: 0;
        }
        &-subscribe {
            margin-right: 0;
        }
    }
}

@include media-breakpoint-down(sm) {
    #{$component-selector} {
        &-search {
            clear: both;
            width: auto;
            &__field {
                width: 100%;
            }
        }
        &-user {
            margin-right: 0;
        }
    }
}

@include media-breakpoint-down(xs) {
    #{$component-selector} {
        &-logo {
            width: 100%;
        }
    }
}
