$font-family-base:            'CircularStd', sans-serif;
$font-family-secondary:       'SF UI Text', sans-serif;

$font-size-base:              0.8125rem; // 13px
$font-size-sm:                $font-size-base * 12 / 13; // 12px
$font-size-xs:                $font-size-base * 11 / 13; // 11px

$h1-font-size:                $font-size-base * 18 / 13; // 18px
$h1-font-size-md:             $font-size-base * 17 / 13; // 17px
$h1-font-size-sm:             $font-size-base * 16 / 13; // 16px
$h2-font-size:                $font-size-base * 16 / 13; // 16px
$h2-font-size-md:             $font-size-base * 15 / 13; // 15px
$h2-font-size-sm:             $font-size-base * 14 / 13; // 14px
$h3-font-size:                $font-size-base * 14 / 13; // 14px
$h4-font-size:                $font-size-base * 13 / 13; // 13px
$h5-font-size:                $font-size-base * 12 / 13; // 12px
$h6-font-size:                $font-size-base;           // 13px

$gray-200:                    #f7f7f8;
$gray-300:                    #9797a6;
$gray-400:                    #666666;
$gray-500:                    #51516B;
$gray-600:                    #444444;
$gray-700:                    #333333;

$body-color:                  $gray-500;

$theme-colors: (
  'primary':                  #007AFF,
  'secondary':                #345666
);

$grid-columns:                24;
$grid-gutter-width:           20px;

$font-weight-semibold:        500;
$font-weight-bold:            600;

$link-color:                  #254353;
$link-hover-color:            darken($link-color, 15%);

$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1268px
);

$border-radius:               3px;
$border-color:                #eaeaea;

$btn-padding-y:               5px;
$btn-padding-x:               20px;

// Pagination

$pagination-padding-y:              0;
$pagination-padding-x:              .25rem;
$pagination-padding-y-sm:           0;
$pagination-padding-x-sm:           .15rem;
$pagination-padding-y-lg:           0;
$pagination-padding-x-lg:           .5rem;
$pagination-line-height:            1.25;

$pagination-color:                  $gray-300;
$pagination-border-width:           0;
$pagination-active-color:           #254352;
$pagination-active-bg:              transparent;
$pagination-hover-color:            #fff;

$color-law: #0500ff;
$color-clause: #8500ff;
$color-anomaly: #ff0500;
$color-date: #ff8500;

$content-border-color:              #efefef;
$modal-backdrop-opacity:            .1;
$modal-content-border-color:        $content-border-color;

$input-border-color:                #c2d1d9;
$input-height:                      40px;
