@font-face {
    font-family: 'SF UI Text';
    src: url('../assets/fonts/SFUIText-Regular.woff2') format('woff2'),
        url('../assets/fonts/SFUIText-Regular.woff') format('woff'),
        url('../assets/fonts/SFUIText-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'SF UI Text';
    src: url('../assets/fonts/SFUIText-Semibold.woff2') format('woff2'),
        url('../assets/fonts/SFUIText-Semibold.woff') format('woff'),
        url('../assets/fonts/SFUIText-Semibold.ttf') format('truetype');
    font-weight: 500;
    font-style: normal;
}

@font-face {
    font-family: 'SF UI Text';
    src: url('../assets/fonts/SFUIText-Bold.woff2') format('woff2'),
        url('../assets/fonts/SFUIText-Bold.woff') format('woff'),
        url('../assets/fonts/SFUIText-Bold.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'CircularStd';
    src: url('../assets/fonts/CircularStd-Book.woff2') format('woff2'),
        url('../assets/fonts/CircularStd-Book.woff') format('woff'),
        url('../assets/fonts/CircularStd-Book.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}
