$component-selector: '.main-sidebar';

#{$component-selector} {
    float: left;
    width: 197px;
    color: $white;
    &__content {
        width: 129px;
        margin-left: 28px;
        margin-top: 75px;
        padding-bottom: 20px;
        border-bottom: 1px solid #5da5f3;
        font-family: $font-family-secondary;
    }
    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        background-image: linear-gradient(106deg, #345666, #244251);
        height: 424px;
        z-index: -1;
    }
    &__list {
        a {
            &, &:hover {
                text-decoration: none;
                color: $white;
            }
            &:hover {
                opacity: 0.8;
            }
        }
    }
    &__item {
        display: inline;
        &.active {
            #{$component-selector}__link {
                &:before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    bottom: 0;
                    background-color: $white;
                    width: 4px;
                    border-radius: 3.2px;
                    box-shadow: 4px 0 7px 0 rgba(48, 48, 48, 0.1);
                }
                &:after {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    bottom: 0;
                    width: 28px;
                    background-image: linear-gradient(to left, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.35));
                }
            }
        }
    }
    &__link {
        position: relative;
        display: block;
        padding: 10px 0 10px 25px;
        margin-bottom: 6px;
    }
    &-additional {
        width: 129px;
        margin: 365px 0 0 28px;
        font-family: $font-family-secondary;
        &_mt {
            border-top: $border-width solid #e9e9ee;
            padding-top: 10px;
            margin-top: 626px;
        }
        &__item {
            &_settings {
                font-family: 'CircularStd';
                margin-bottom: 15px;
                padding-bottom: 15px;
                border-bottom: solid $border-width $border-color;
                font-size: $font-size-base;
                #{$component-selector}-additional__link {
                    i {
                        font-size: 18px;
                        margin-right: 10px;
                        vertical-align: top;
                    }
                }
            }
        }
        &__link {
            display: block;
            padding: 5px 0 5px 25px;
            font-size: $font-size-xs;
            &, &:hover {
                color: #acacbf;
            }
        }
    }
}

@include media-breakpoint-down(sm) {
    #{$component-selector} {
        float: none;
        width: auto;
        &__content {
            margin: 0;
            width: auto;
        }
        &__list {
            display: flex;
            justify-content: space-between;
        }
        &__link {
            padding: 5px 10px;
        }
        &-additional {
            position: relative;
            z-index: 1;
            width: auto;
            display: flex;
            justify-content: space-between;
            margin: 15px 0 0;
            &_mt {
                border-top: none;
                margin-top: 15px;
            }
            &__link {
                line-height: 20px;
                padding: 0;
                &, &:hover {
                    color: $white;
                }
                i {
                    font-size: 14px !important;
                    position: relative;
                    top: 3px;
                }
            }
            &__item {
                &_settings {
                    font-family: $font-family-secondary;
                    margin: 0;
                    padding: 0;
                    border-bottom: none;
                    font-size: $font-size-xs;
                }
            }
        }
    }
}
