@import 'modules/all';

@import 'bootstrap/scss/functions';
@import 'bootstrap/scss/variables';
@import 'bootstrap/scss/mixins';

@import 'bootstrap/scss/reboot';
@import 'bootstrap/scss/type';
@import 'bootstrap/scss/grid';
@import 'bootstrap/scss/utilities';
@import 'bootstrap/scss/buttons';
@import 'bootstrap/scss/forms';
@import 'bootstrap/scss/tables';
@import 'bootstrap/scss/pagination';
@import 'bootstrap/scss/transitions';
@import 'bootstrap/scss/modal';
@import 'bootstrap/scss/images';
@import 'bootstrap/scss/images';

@import 'mixins/all';
@import 'partials/all';

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
    font-family: $font-family-secondary;
}


h1, .h1 {
    font-weight: $font-weight-bold;
}
h2, .h2 {
    font-weight: $font-weight-bold;
    margin-bottom: 0.3rem;
}

a {
    transition: all 0.2s linear;
}

html, body {
    position: relative;
    height: 100%
}
body {
    min-width: 320px;;
    position: relative;
    background-color: #F2F6FA;
    letter-spacing: 0.15px;
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.wrapper {
    position: relative;
    max-width: 100%;
    min-height: 100%;
    overflow: hidden;
}

input[type=checkbox].custom-checkbox, input[type=radio].custom-checkbox {
    position: absolute;
    z-index: -1000;
    left: -2000px;
    overflow: hidden;
    clip: rect(0 0 0 0);
    height: 1px;
    width: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
    & + label.custom-checkbox__label {
        cursor: pointer;
        padding-left: 29px;
        display: inline-block;
        line-height: 16px;
        background-repeat: no-repeat;
        background-position: 0 0;
        vertical-align: middle;
        margin-bottom: 0;
        text-indent: -29px;

        &:before {
            content: '';
            display: inline-block;
            width: 16px;
            height: 16px;
            background-image: url(../assets/img/checkbox.png);
            vertical-align: top;
            position: relative;
            top: -1px;
        }
    }
    &:checked + label.custom-checkbox__label {
        &:before {
            background-position: 0 -16px;
        }
    }
}

label.custom-checkbox__label {
    -webkit-touch-callout: none;
    user-select: none;
}

.main-content {
    margin: 22px 16px 22px 197px;
    &__head {
        color: $white;
        overflow: hidden;
    }
    &-head-folder {
        margin-top: 25px;
        &__name {
            font-family: $font-family-secondary;
            font-size: $font-size-xs;
        }
        &__icon {
            vertical-align: top;
            font-size: 18px;
            margin-left: 12px;
        }
    }
    &__title-small {
        font-family: $font-family-secondary;
        font-size: $font-size-xs;
        margin-bottom: 12px;
    }
}

.main-section {
    background-color: $white;
    border-radius: $border-radius;
    box-shadow: 0 7px 36px 0 rgba(48, 48, 48, 0.13);
    border: solid 1px $content-border-color;
    padding: 26px 42px 65px 23px;
    overflow: hidden;
    &__title {
        border-bottom: $border-width solid $border-color;
        padding-bottom: 30px;
    }
    &__title-left {
        padding-left: 15px;
    }
    &__title-small {
        font-family: $font-family-secondary;
        font-weight: bold;
        color: $gray-300;
        letter-spacing: -0.15px;
    }
    &_analysator {
        padding-bottom: 26px;
    }
}
.main-actions {
    padding: 22px 0;
    margin-left: 10px;
    &__grey {
        .button-action {
            color: $gray-300;
        }
    }
    &__left {
        .button-action {
            margin-right: 24px;
        }
    }
    &__right {
        .button-action {
            margin-left: 24px;
        }

    }
}

.button-action {
    border: 0;
    outline: none;
    background: transparent;
    color: $link-color;
    font-family: $font-family-secondary;
    font-weight: bold;
    font-size: $font-size-xs;
    margin: 0 12px;
    &__icon {
        margin-right: 5px;
    }
    &__text {
        text-decoration: underline;
    }
    &:hover {
        span {
            text-decoration: none;
        }
    }
    &:first-child {
        margin-left: 0;
    }
    &:last-child {
        margin-right: 0;
    }
}

.table-files {
    min-width: 500px;

    thead {
        color: $gray-300;
        font-size: $font-size-xs;
        tr {
            background-color:$gray-200;
        }
        td {
            padding: 8px 0;
            border-bottom: solid $border-width $border-color;
        }
    }
    tbody {
        tr:nth-child(even){
            background-color:$gray-200;
        }
        td {
            border: none;
            padding: 5px 0;
        }
    }
    td {
        vertical-align: middle;
    }
    &__folder {
        font-weight: bold;
        font-family: $font-family-secondary;
    }
    &__icon {
        font-size: 18px;
        margin-right: 10px;
        vertical-align: middle;
    }
    &__analysis {
        font-size: 16px;
    }
}

.pagination {
    justify-content: flex-end;
    font-size: 15px;
    font-weight: bold;
    color: $gray-300;
    margin-top: 30px;
    .page-link.active {
        font-family: $font-family-secondary;
        color: #254352;
    }
}

.main-document-info {
    font-family: $font-family-secondary;
    color: $gray-300;
    font-weight: bold;
    margin: -5px 0 0 15px;
    &__language {
        margin-left: 20px;
    }
}

.main-title-tab {
    text-align: right;
    border-bottom: solid 1px #eaeaea;
    margin-top: -15px;
    &__name {
        color: #2a4959;
        font-weight: bold;
        padding: 8px 20px 6px;
        display: inline-block;
        background-color: $gray-200;
    }
    &__icon {
        vertical-align: top;
        position: relative;
        top: 2px;
        font-size: 15px;
        margin-right: 5px;
    }
    &__text {
        letter-spacing: 0.15px;
    }
}

.main-analysator {
    margin-top: 32px;
    &-container {
        margin-right: 164px;
        padding: 36px 55px;
        background-color: $gray-200;
    }
    &-content {
        font-size: $font-size-xs;
        font-family: $font-family-secondary;
        color: $black;
        background-color: $white;
        padding: 34px 39px;
        line-height: 14px;
        letter-spacing: 0.5px !important;
        position: relative;
    }
    &-sidebar {
        float: right;
        width: 164px;
        padding-left: 24px;

        &__head {
            cursor: pointer;
            position: relative;
            padding-left: 15px;
            margin: 0 0 11px;
            &:before {
                content: '';
                position: absolute;
                overflow: hidden;
                background: url('data:image/png;base64,');
                width: 9px;
                height: 6px;
                top: 6px;
                left: -2px;
            }
            &:hover {
                text-decoration: underline;
            }
            &.collapsed {
                &:before {
                    top: 5px;
                    left: 0;
                    background: url('data:image/png;base64,');
                    width: 6px;
                    height: 9px;
                }
            }
            &_law {
                color: $color-law;
            }
            &_clause {
                color: $color-clause;
            }
            &_anomaly {
                color: $color-anomaly;
            }
            &_date {
                color: $color-date;
            }
        }
        &__list {
            margin-left: 15px;
            font-family: $font-family-secondary;
            font-weight: 500;
            padding: 25px 0;
            font-size: $font-size-xs;
            &, a {
                color: $gray-300;
            }
        }
        &__item {
            margin: 0 0 13px;
        }

    }
    .highlight {
        &:before {
            color: $white;
            display: block;
            position: absolute;
            left: -30px;
            margin-top: -16px;
            line-height: 16px;
            padding: 0 8px;
            border-radius: 10px;
            font-family: $font-family-secondary;
            font-weight: bold;
        }
        &_law {
            background-color: lighten($color-law, 40%);
            &:before {
                content: 'Law';
                background-color: $color-law;
            }
        }
        &_clause {
            background-color: lighten($color-clause, 40%);
            &:before {
                content: 'Clause';
                background-color: $color-clause;
            }
        }
        &_anomaly {
            background-color: lighten($color-anomaly, 40%);
            &:before {
                content: 'Anomaly';
                background-color: $color-anomaly;
            }
        }
        &_date {
            background-color: lighten($color-date, 40%);
            &:before {
                content: 'Date';
                background-color: $color-date;
            }
        }
    }
}

.partial-section {
    position: relative;
    font-family: $font-family-secondary;
    box-shadow: 0 7px 36px 0 rgba(48, 48, 48, 0.1);
    border: solid 1px $content-border-color;
    background-color: $white;
    border-radius: $border-radius;
    padding: 16px;

    h2 {
        color: #304f5f;
        letter-spacing: 0.22px;
        margin-bottom: 20px;
    }

    &__expand {
        position: absolute;
        z-index: 10;
        cursor: pointer;
        border: none;
        outline: none;
        top: 8px;
        right: 8px;
        background: url('data:image/png;base64,');
        width: 16px;
        height: 16px;
        overflow: hidden;
    }
}

.main-auth {
    font-family: $font-family-secondary;
    margin-top: 65px;
    height: 736px;
    border-radius: $border-radius;
    box-shadow: 0 7px 36px 0 rgba(48, 48, 48, 0.13);
    border: solid $border-width #efefef;
    background-color: $white;
    &__white {
        position: relative;
        border: solid 1px #efefef;
        height: 100%;
        z-index: 1;
    }
    &__grey {
        position: relative;
        background-color: #f2f6fa;
        height: 100%;
        &:before {
            content: '';
            position: absolute;
            background-image: url(../assets/img/auth-path.png);
            width: 411px;
            height: 361px;
            bottom: 0;
            right: -113px;
        }
    }
    &__small {
        margin-bottom: 30px;
    }
    &__title {
        margin-bottom: 32px;
    }
    &__content {
        width: 350px;
        max-width: 100%;
        padding: 0 10px 75px;
        margin: 0 auto;
    }
    &__submit {
        margin-top: 40px;
        text-align: center;
        .btn {
            width: 175px;
            height: 40px;
            border-radius: 20px;
            text-transform: uppercase;
            font-size: $font-size-xs;
            font-weight: bold;
        }
    }
    &__info {
        margin-top: 22px;
        text-align: center;
        a {
            color: #007aff;
            font-weight: bold;
        }
    }
    &_subscribe {
        .main-auth {
            &__content {
                padding-bottom: 50px;
            }
            &__grey {
                &:before {
                    transform: scaleX(-1);
                    right: auto;
                    left: -113px;
                }
            }
            &__title {
                margin-bottom: 15px;
                line-height: 24px;
                color: #315262;
            }
        }
    }
}

@include media-breakpoint-down(md) {
    h1, .h1 {
        font-size: $h1-font-size-md;
    }
    h2, .h2 {
        font-size: $h2-font-size-md;
    }
    .main-actions {
        .button-action {
            margin: 0;
        }
    }
    .main-analysator {
        margin-top: 32px;
        &-container {
            margin-right: 0;
            padding: 20px 30px;
        }
        &-content {
            padding: 10px;
        }
        &-sidebar {
            float: none;
            width: auto;
            padding-left: 0;
            margin-bottom: 15px;
            &__head {
                margin-bottom: 5px;
            }
            &__list {
                padding: 5px 0 10px;
            }
            &__item {
                margin: 5px 0;
            }
        }
        .highlight {
            &:before {
                left: -50px;
            }
        }
    }
    .main-auth {
        margin-top: 10px;
    }
}

@include media-breakpoint-down(sm) {
    h1, .h1 {
        font-size: $h1-font-size-sm;
    }
    h2, .h2 {
        font-size: $h2-font-size-sm;
    }
    .main-content {
        margin: 20px 0;
        &-head-folder {
            margin: 10px 0;
        }
    }
    .main-section {
        padding: 20px;
        &__title-left {
            padding-left: 0;
            margin-bottom: 10px;
        }
    }
    .main-actions {
        margin-left: 0;
    }
    .button-action {
        margin: 0;
    }
    .pagination {
        justify-content: center;
    }
}
